  height: 50px;
  margin-top: -25px;
  margin-left: -25px; }
  .is-loading .c-load-spinner {
    display: block;
    animation: rotate 1.2s infinite linear; }
  .c-load-spinner::before, .c-load-spinner::after  {
    width: 60%;
    height: 60%;
    display: inline-block;
    position: absolute;
    top: 0;
    border: 4px solid #ee7844;
    border-radius: 50%;
    animation: bounce 1.2s infinite ease-in-out;
    content: " "; }
  .c-load-spinner::after {
    top: auto;
    bottom: 0;
    animation-delay: -0.6s; }

@keyframes rotate {
  100% {
    transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes bounce {
  0%, 100% {
    transform: scale(0); }
  50% {
    transform: scale(1); } }

