  font-size: 0; }

@media (max-width: 480px) {
  .c-gallery__item:nth-child(n+3) {
    position: absolute;
    top: 0%;
    left: 50%;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(1px, 1px, 1px, 1px); } }

@media (max-width: 1024px) {
  .c-gallery__item:nth-child(n+4) {
    position: absolute;
    top: 0%;
    left: 50%;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(1px, 1px, 1px, 1px); } }

@media (min-width: 1025px) {
  .c-gallery__item:nth-child(n+5) {
    position: absolute;
    top: 0%;
    left: 50%;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(1px, 1px, 1px, 1px); } }

.c-gallery-item__overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: -1px;
  /** subpixel error */
  left: 1.5rem;
  display: none;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  cursor: pointer;
  font-weight: bold; }

@media (max-width: 480px) {
  .c-gallery-item__overlay\@xs-down {
    display: block; } }

@media (max-width: 1024px) {
  .c-gallery-item__overlay\@md-down {
    display: block; } }

@media (min-width: 1025px) {
  .c-gallery-item__overlay\@md-up {
    display: block; } }

.how-it-works {
