
@mixin btn-border($color) {
	position: relative;
	&::before {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		border: 1px solid $color;
		border-radius: $brand-radius;
		transition: border $brand-transition;
		content: " ";
	}
}
@if ($use-buttons) {
